import React, {useCallback, useEffect} from 'react';
import {useDispatch, useSelector} from "react-redux";
import {useParams} from 'react-router-dom';
import cl from "../groupUsers/groupUsers.module.css";
import {AppRootStateType} from "../../../bll/store";
import {GroupType} from "../../../bll/Api";
import {
    getGroupByIdTC,
    groupUsersType,
    removeGroupsTC,
    removeTodolistFromGroupTC,
    removeUserFromGroupTC
} from "../../../bll/groupReduser";
import {TodolistsList} from "../todo/todoList";
import DeleteIcon from "@material-ui/icons/Delete";
import {addTodolistTC, removeTodolistTC} from "../../../bll/todolists-reducer";
import {removeGroupFromUserDataTC} from "../../../bll/userReduser";


const GroupPage = React.memo(() => {
    const dispatch = useDispatch()
    const {id}: any = useParams()
    useEffect(() => {dispatch(getGroupByIdTC(id))},[dispatch, id])

    const group = useSelector<AppRootStateType, GroupType>(state => state.groups.activeGroup)
    const deleteGroup = () => {
        dispatch(removeGroupsTC(id))
    }
    let user = group.data.users?.map((u: groupUsersType) => {
        const removeUser = () => {
            const user = {uid: u.uid, name: u.name}
            dispatch(removeUserFromGroupTC(id, {uid: u.uid, name: u.name}))
            dispatch(removeGroupFromUserDataTC(u.uid))
        }
        return (
            <div key={u.uid}>
                {/*<p>{u.uid}</p>*/}
                <span>{u.name}</span>
                <DeleteIcon onClick={removeUser} color='primary'/>
            </div>
        )
    })
    const addTodolist = useCallback((title: string) => {
        dispatch(addTodolistTC(title, id))
    }, [dispatch])
    const removeTodolist = useCallback(function (todolistId: string) {
        dispatch(removeTodolistTC(todolistId))
        dispatch(removeTodolistFromGroupTC(id, todolistId))
    }, [dispatch])
    return (
        <>
            <div style={{display: 'flex'}}>
                <p>name groups:</p><h2> {group.data.group}</h2>
            </div>
            <div className={cl.box}>
                {/*<Button onClick={deleteGroup}>delete</Button>*/}
                {user && <><h5>list users:</h5>
                    <div style={{display: 'flex'}}>{user}</div>
                </>}
                <h5>content for group: </h5>
                <TodolistsList addTodolist={addTodolist} todolistFromGroup={group.data.todoLists}
                               removeTodolist={removeTodolist}/>
            </div>
        </>
    );
});

export default GroupPage;