import React, {ChangeEvent, useCallback, useState} from 'react';
import {useDispatch, useSelector} from "react-redux";
import {AppRootStateType} from "../../../bll/store";
import {updateUserTC} from "../../../bll/userReduser";
import cl from './user.module.css'
import { createStyles, FormControl, FormHelperText, Select, Theme} from "@material-ui/core";
import {EditableSpan} from "../../components/common/EditableSpan";
import makeStyles from "@material-ui/core/styles/makeStyles";
import {GroupType} from "../../../bll/Api";
import {groupUsersType, removeUserFromGroupTC} from "../../../bll/groupReduser";
import DeleteIcon from "@material-ui/icons/Delete";
import UpdateIcon from '@material-ui/icons/Update';
import AddBoxIcon from '@material-ui/icons/AddBox';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        formControl: {
            margin: theme.spacing(1),
            minWidth: 150,
        },
        selectEmpty: {
            marginTop: theme.spacing(2),
        },
    }),
);
type propsType = {
    uid: string
    name?: string | undefined
    phoneNumber?: string
    date: string
    email: string
    groupName: string | null
    groupId: string | null
    listTasks?: {} | undefined
    removeUser: (uid: string) => void
    addUserToGroup: (uid: string, groupName: string, name: string, user: groupUsersType) => void
    listGroup: Array<GroupType>
}
const User = (props: propsType) => {
    const classes = useStyles();
    const dispatch = useDispatch()
    const admin = useSelector<AppRootStateType, string>(state => state.users.adminUid)
    const curentUser = useSelector<AppRootStateType, string>(state => state.users.userid)
    let [groupID, setGroupID] = useState<string>('')
    let [name, setName] = useState<string>(props.name ? props.name : 'not a name')
    let [email, setEmail] = useState<string>(props.email)
    let [phone, setPhone] = useState<string>(props.phoneNumber ? props.phoneNumber : 'not a phoneNumber')
    const changeEmail = (email: string) => {
        setEmail(email)
    }
    const changeName = (name: string) => {
        setName(name)
    }
    const changePhone = (phone: string) => {
        setPhone(phone)
    }
    const removeUser = () => {
        const user = {uid: props.uid, name}
        if(props.groupId){
            dispatch(removeUserFromGroupTC(props.groupId,user))
        }
        props.removeUser(props.uid)
    }
    const update = useCallback(() => {
        let payload = {email, name, phone}
        dispatch(updateUserTC(props.uid, payload))
    }, [email, name, phone])
    const addToo = () => {
        const user = {uid: props.uid, name}
        if(props.groupId){
            // debugger
            dispatch(removeUserFromGroupTC(props.groupId,user))
        }
        props.addUserToGroup(groupID, props.uid, name, user)
    }
    const styles = {
        // display:!admin  ?  'block' : 'none'
    }
    const handleChange = (event: React.ChangeEvent<{ value: unknown }>) => {
        setGroupID(event.target.value as string);
    };
// debugger
    return (
        <div key={props.uid} >
            {/*<p>uid:{props.uid}</p>*/}
            {admin !== curentUser && <>
                <div className={cl.spanWrap}><span>name:</span><h5>{name ? name : 'no name'}</h5></div>
                <div className={cl.spanWrap}><span>email:</span><h5>{props.email}</h5></div>
            </>}
            {admin === curentUser &&
            <div className={cl.TableRow} style={styles}>
                <div className={`${cl.large} ${cl.wrap}`}>
                    <EditableSpan onChange={changeEmail} value={email}/>
                    <EditableSpan onChange={changeName} value={name}/>
                    <EditableSpan onChange={changePhone} value={phone}/>
                </div>
                <div className={`${cl.medium} ${cl.wrap}`}>
                    <UpdateIcon onClick={update} color='primary' />
                    <DeleteIcon onClick={removeUser} color='primary'/>
                <div className={cl.spaceBetween}>
                    <FormControl className={classes.formControl}>
                        <Select labelId="demo-simple-select-label" id="demo-simple-select"
                                value={groupID} onChange={handleChange}>
                            {props.listGroup.map(g => {
                                return <option value={g.id} key={g.id}>{g.data.group}</option>
                            })}
                        </Select>
                        <FormHelperText>select group</FormHelperText>
                    </FormControl>
                    {/*<Button onClick={addToo}>add to group</Button>*/}
                    <AddBoxIcon onClick={addToo} color='primary'/>
                </div>
                </div>
                <div className={`${cl.small} ${cl.wrap}`}>
                    {/*<p>date :{props.date}</p>*/}
                    <p>{props.groupName ? props.groupName : 'no group'}</p>
                    <p>{props.listTasks ? props.listTasks : 'no tasks'}</p>
                </div>
            </div>
            }
        </div>
    );
};

export default User;

