import {AnyAction, Dispatch} from "redux";
import {GroupType, Users, GroupsApi} from "./Api";
import * as uuid from 'uuid';
import {getAllUsersFirestoreTC} from "./userReduser";

export type setGroupsACType = ReturnType<typeof setGroupsAC>
type addGroupsACType = ReturnType<typeof addGroupsAC>
type deleteGroupsACType = ReturnType<typeof deleteGroupsAC>
type getGroupByIdACType = ReturnType<typeof getGroupByIdAC>
type actionType = setGroupsACType | addGroupsACType | deleteGroupsACType | getGroupByIdACType
export type  groupUsersType = {
    uid: string
    name?: string
}

type initStateType = {
    groups: GroupType[]
    activeGroup: GroupType
    status: boolean
    userRecord: string
    message: string
}

export let initState: initStateType = {
    groups: [],
    activeGroup: {id: 'ggggggg', data: {group: 'string', users: [],todoLists:[]}},
    // activeGroup: {} as GroupType,
    status: false,
    userRecord: '',
    message: '',
}

export const groupReducer = (state = initState, action: actionType) => {
    switch (action.type) {
        case 'GROUP/SET-GROUPS':
            return {...state, groups: action.payload}
        case 'GROUP/ADD-GROUP':
            return {...state, groups: [...state.groups, action.payload]}
        case 'GROUP/DELETE-GROUP':
            return {...state, groups: [state.groups.filter(g => g.id !== action.payload)]}
        case "GROUP/GET-GROUP-WITH-ID": {
            return {
                ...state,
                activeGroup: action.payload
            }
        }
        default:
            return {...state}
    }
}
export const setGroupsAC = (payload: any) => ({type: 'GROUP/SET-GROUPS', payload} as const)
export const addGroupsAC = (payload: any) => ({type: 'GROUP/ADD-GROUP', payload} as const)
export const deleteGroupsAC = (payload: any) => ({type: 'GROUP/DELETE-GROUP', payload} as const)
export const getGroupByIdAC = (payload: GroupType) => ({type: 'GROUP/GET-GROUP-WITH-ID', payload} as const)

//TC
export const getGroupsTC = () => (dispatch: Dispatch) => {
    GroupsApi.getGroups()
        .then(res => {
                dispatch(setGroupsAC(res))
            }
        )
}
export const getGroupByIdTC = (id: string) => (dispatch: Dispatch) => {
    // debugger
    GroupsApi.getGroupById(id)
        .then(res => {
                // console.log('group.getGroupByIdAC;' + parseInt(res));
                // dispatch(getGroupByIdAC(res))
                dispatch(getGroupByIdAC({data: res, id}))
            }
        )
}
export const removeGroupsTC = (id: string) => (dispatch: any) => {
    GroupsApi.groupRemove(id).then(res => {
        dispatch(getGroupsTC())
        console.log(res);
    })
}
export const addGroupsTC = (group: string) => (dispatch: any) => {
    let id = uuid.v1()
    GroupsApi.addGroup(id, group)
        .then(res => {
            dispatch(getGroupsTC())
            console.log(res);
        })
}
export const removeUserFromGroupTC = (id: string,user: groupUsersType) => (dispatch: any) => {
   // debugger
    GroupsApi.removeUserFromGroup(id, user).then(res => {
        dispatch(getGroupByIdTC(id))
        console.log(res);
    })

}
export const removeTodolistFromGroupTC = (id: string,todolistId:string) => (dispatch: any) => {
    GroupsApi.removeTodoFromGroup(id, todolistId).then(res => {
        dispatch(getGroupByIdTC(id))
        console.log(res);
    })
}
