import React, {useCallback, useEffect} from 'react';
import {useDispatch, useSelector} from "react-redux";
import {
    removeUserTC,
    setUserOnGroupTC,
    setUsersTC,
    userType
} from "../../../bll/userReduser";
import {AppRootStateType} from "../../../bll/store";
import User from "../users/user";
import cl from './user.module.css';
import {GroupType} from "../../../bll/Api";
import {getGroupsTC, groupUsersType} from "../../../bll/groupReduser";
import TableContainer from "@material-ui/core/TableContainer";
import Paper from "@material-ui/core/Paper";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import TableBody from "@material-ui/core/TableBody";
import {makeStyles} from "@material-ui/core/styles";


const useStyles = makeStyles((theme) => ({
    head: {
        backgroundColor: theme.palette.common.black,
        color: theme.palette.common.white,
    },
    table: {
        minWidth: 650,
    },
}));
const Users = () => {
    const dispatch = useDispatch()
    useEffect(() => {
        dispatch(setUsersTC())
        dispatch(getGroupsTC())
    }, [dispatch])
    const removeUser = useCallback(function (uid: string) {
        const action = removeUserTC(uid)
        dispatch(action)
    }, [])
    const addUserToGroup = (id: string, uid: string, userName: string, user: groupUsersType) => {
        // dispatch(setUserOnGroupTC(id,uid, userName))
        dispatch(setUserOnGroupTC(id, uid, userName, user))
    }
    const allUsers = useSelector<AppRootStateType, Array<userType>>(state => state.users.users)
    const allGroups = useSelector<AppRootStateType, Array<GroupType>>(state => state.groups.groups)
    const user = allUsers.map(u => <User key={u.uid} groupName={u.groupName} groupId={u.groupId} name={u.displayName} date={u.tokensValidAfterTime}
                                         email={u.email} listTasks={u.listTasks} uid={u.uid} removeUser={removeUser}
                                         addUserToGroup={addUserToGroup} phoneNumber={u.phoneNumber}
                                         listGroup={allGroups}/>)
    return (
        <div>
            <input type="text"/>
            <h5>all users: </h5>
            <p>.   ( email  name )   </p>
            {/*<div className={cl.wrap}>{user}</div>*/}
            <div>
                <div className={cl.table} aria-label="a dense table">
                    <div className={cl.TableHead}>
                        <div className={cl.TableRow}>
                            <div className={cl.large}>
                                <h4>Users (data)</h4>
                                <span className={cl.wrap}>
                                    <p>email</p>
                                    <p>name</p>
                                    <p>phoneNumber</p>
                                </span>
                            </div>
                            <div className={cl.medium}>
                                <h4>buttons group</h4>
                                <span className={cl.wrap}>
                                    <p>change/add group</p>
                                    <p>change/delet user</p>
                                </span>
                            </div>
                            <div className={cl.small}>
                                <h4>Users (info)</h4>
                                <span className={cl.wrap}>
                                    <p>group</p>
                                    <p>task</p>
                                </span>
                            </div>
                        </div>
                    </div>
                    <div className={cl.TableBody}>
                        {user}
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Users;
